@tailwind base;
@tailwind components;
@tailwind utilities;

@import url('https://fonts.googleapis.com/css2?family=Caveat:wght@400;500;600;700&family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,900&display=swap');

:root {
    --primary-dark: #5651e5;
    --primary-light: #709dff;
  }
  
  .icon {
    color: var(--primary-dark);
  }

@layer base {
    

    body {
        @apply font-[Montserrat]
    }

    li {
        @apply p-4 
    }

    h1 {
        @apply text-2xl md:text-4xl  sm:text-xl font-bold 
    }

    h2 {
        @apply text-3xl font-bold
    }

    h3 {
        @apply text-xl font-bold
    }

    button {
        @apply p-3 border bg-gradient-to-r from-[var(--primary-dark)] to-[var(--primary-light)] text-white rounded-md
      }
      .icon {
        @apply text-2xl cursor-pointer
      }
};